.account-name-container {
  background-color: rgb(105, 172, 105); /* Light background for contrast */
  padding: 2px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  max-width: 250px; /* Limit width to prevent it from stretching */
  margin: 0 auto; /* Center the container horizontally */
  margin-left: 25px;
  height: 20px;
  text-align: center; /* Center the text within the container */
}

.account-name {
  font-size: 15px; /* Adjust font size as needed */
  font-weight: 500;
  color: #0467be; /* Set a color that contrasts well */
  margin: 0;
  white-space: nowrap; /* Prevent text from wrapping */
  overflow: hidden;
  text-overflow: ellipsis; /* Show ellipsis for long names */
}
.error-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.error-box {
  background: white;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  width: 300px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.error-box img.error-icon {
  width: 50px;
  height: 50px;
  margin-bottom: 10px;
}

.error-box p {
  color: red;
  font-size: 14px;
  margin-bottom: 20px;
}

.error-close-button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}

.error-close-button:hover {
  background-color: #0056b3;
}
